body{margin:0 auto;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;position:relative;background:#fcf5f5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.header{display:flex;justify-content:space-between}.header__logo{display:flex;align-items:center}.header__menu{font-family:Hero;display:flex;align-items:center;font-size:18px}.header__menu .tab{margin-right:45px;list-style:none}.header__menu .tab-link{transition:0.3s;text-decoration:none;color:#035755}.header__menu .tab-link:hover{cursor:pointer;color:#fa5652}.header__menu .tab:last-child{margin-right:0}.header-position{margin-top:35px}.logo__text{font-family:RedHatDisplay;font-weight:bold;font-size:19px;color:#035755;margin-left:10px}

.covid-alert{display:flex;justify-content:space-between;align-items:center}.covid-alert__text{font-size:21px;width:640px;font-family:Hero}.covid-alert__img{width:680px}.covid-alert-position{margin-top:35px}

.what-is-covid{display:flex;align-items:center}.what-is-covid__img{width:670px}.what-is-covid__text{font-size:21px;width:640px;font-family:Hero}

.contagion__text{font-family:Hero;display:flex;justify-content:center;flex-direction:column;text-align:center;font-size:21px}.contagion__text .title{text-align:center}.contagion .contagion-cards{display:flex;justify-content:center}.contagion .card{width:410px;height:525px;margin-right:30px;border-radius:7px;box-shadow:1px 2px 10px 1px #cccccc;transition:400ms}.contagion .card:last-child{margin-right:0}.contagion .card__img{height:249px;margin-top:50px;display:flex;align-items:center;justify-content:center}.contagion .card__text{font-family:Hero;margin:35px auto;display:flex;flex-direction:column;justify-content:center;text-align:center;align-items:center;font-size:21px;color:#4b4f51;width:370px}.contagion .card__text-bold{font-family:Bebas;margin-bottom:20px;font-weight:bold;font-size:36px}.contagion-position{margin-bottom:100px}.card:hover{box-shadow:6px 7px 20px 1px #797b7c;cursor:pointer}

.symptomps__text{display:flex;justify-content:center;flex-direction:column;align-items:center;font-family:Hero}.symptomps__text .title{text-align:center}.symptomps__text .text{text-align:center;font-size:21px}.symptomps__img{display:flex;flex-direction:column;justify-content:center;align-items:center}.symptomps__img .img-smpt-bg{position:absolute;width:600px;z-index:-1}.symptomps__img .img-smpt{width:800px;margin-top:50px}.symptomps-position{margin-bottom:100px}

.what-should-do__text{display:flex;justify-content:center;flex-direction:column;align-items:center;margin-bottom:40px}.what-should-do__text .title{text-align:center}.what-should-do__text .text{font-family:Hero;text-align:center;font-size:21px}.what-should-do .protected{display:flex;align-items:center;justify-content:center;flex-direction:column}.what-should-do .protected__item{width:1300px;display:flex;align-items:center;justify-content:space-around}.what-should-do .protected__info{display:flex;width:670px;height:250px;font-size:21px;line-height:36px;font-family:Hero}.what-should-do .protected__info .info-number{text-align:center;font-family:RedHatDisplay;font-size:44px;color:#fa5652;background:#f5bcba;border-radius:50%;width:75px;height:75px;line-height:70px}.what-should-do .protected__info .info-text{width:550px;font-family:Hero;margin-left:40px}.what-should-do .protected__info .info-text__title{font-family:Bebas;font-weight:bold;color:#4b4f51;font-size:47px}.what-should-do .protected__img .girl{width:500px}

.help{display:flex;justify-content:center;align-items:center;text-align:center;flex-direction:column}.help__title .title{margin:20px 0;font-family:Bebas;color:#167c51}.help-sub{display:flex}.help__post{height:50px;width:430px;border:solid 1px #ccc;box-shadow:1px 1px 3px 1px #cccccc;border-radius:30px;margin-right:20px;transition:0.35s}.help__post:focus-within{box-shadow:2px 2px 17px 0px #636262}.help__post .email-input{font-family:RedHatDisplay;font-size:21px;background-color:inherit;margin-top:5px;width:400px;height:38px;border:none;outline:none}.help__button{width:130px;height:50px;border:none;border-radius:30px;background:inherit;font-family:RedHatDisplay;color:#ffffff;background:#f44a45;font-size:17px;outline:none;transition:0.5s}.help__button:hover{cursor:pointer;box-shadow:1px 1px 3px 1px #ff3636}.help-position{margin-bottom:140px}

.facebook-icon{height:44px;fill:#696969}.facebook-icon-hover:hover{transition:all 350ms;fill:#385694;cursor:pointer}

.youtube-icon{height:45px;fill:#696969}.youtube-icon-hover:hover{transition:all 350ms;fill:red;cursor:pointer}

.vimeo-icon{width:45px;fill:#696969}.vimeo-icon-hover:hover{transition:all 350ms;fill:#41c3ed;cursor:pointer}

.twitter-icon{width:45px;fill:#696969}.twitter-icon-hover:hover{transition:all 350ms;fill:#41ABE1;cursor:pointer}

.footer{display:flex;justify-content:space-between}.footer__menu{font-family:Hero;display:flex;align-items:center;font-size:18px}.footer__menu .tab{margin-right:45px;transition:0.3s;list-style:none}.footer__menu .tab:last-child{margin-right:0}.footer__menu .tab-link{transition:0.3s;text-decoration:none;color:#035755}.footer__menu .tab-link:hover{cursor:pointer;color:#fa5652}.footer__socials{margin-top:53px;display:flex;list-style:none;justify-content:flex-end}.footer__socials .social-tab{margin-right:29px}.footer__socials .social-tab:last-child{margin-right:0}.footer__logo{display:flex;align-items:center}.footer-position{margin-bottom:70px}

html{scroll-behavior:smooth}.title{font-family:Bebas;font-style:normal;font-weight:bold;font-size:47px;color:#4b4f51;margin:0}.title-orange{font-size:21px;color:#fa5652}.content{margin:0 auto;width:1290px}

